:root {
  --main-color: #72b626;
}

/* Start Global Style to Whole Website */
* {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  outline: none;
  scroll-behavior: smooth;
}

.container {
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.page-title {
  text-transform: uppercase;
  position: fixed;
  width: 100%;
  z-index: 1;
  background-color: #eee;
}

.page-title h1 {
  text-align: center;
